body {
    min-width: 375px;
    margin: 0 auto;
    padding: 0;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    line-height: 24px;
    text-align: center;

    background-color: rgb(255, 255, 255);
}

.container {
  padding: 0;
}

.visually-hidden:not(:focus):not(:active) {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;

    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

img {
    max-width: 100%;
    height: auto;
}

ul, ol {
  list-style: none;

  margin: 0;
  padding: 0;
}
